.app-wrapper {
    display: grid;
    max-width: 1200px;
    margin: 0 auto;
    grid-template-areas:
            "h h"
    "n c";
    grid-template-rows: 60px 1fr;
    grid-auto-columns: 2fr 10fr;
    grid-gap: 10px;

}

.app-wrapper-content {
    grid-area: c;
    background-color: aqua;
    border-radius: 10px;

}